.root {
}

.text {
  display: flex;
  flex-direction: row;
  gap: 2px;
  padding: 2px 0;
}

.text .form {
  flex: 1;

  display: flex;
  flex-direction: row;
  gap: 2px;
  padding: 2px 0;
}

.text .name {
  min-width: 120px;
}

.text .input {
  flex: 1;
}

.text .input.singleLine {
}

.text .input.multiLine {
  min-height: 50px;
}

.text .buttons {
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding: 2px 0;
}

.align {
  display: flex;
  flex-direction: row;
  gap: 2px;
  padding: 2px 0;
}
